@import "../../colors.scss";

.button {
  display: flex;
  justify-content: center;
  
  & label {
    background-color: transparent !important;
  }

  &__content {
    justify-content: center;
    align-items: center;
    display: flex;

    padding: .75em;
    padding-top: 0.8em;
    padding-bottom: 0.8em;
  }

  &__divider {
    display: inline-flex;
    padding: 4% 0;
  }
}