@import "../../colors.scss";

.textarea {
  border: 0px;
  line-height: 1.5;
  font-family: inherit;
  resize: vertical;
  min-height: 50px;
  height: 150px;
  width: calc(100% - 30px);
  background-color: $grijs;
  border-radius: 0.25em;
  padding: 0.6em 0.8em;
  font-size: 1em;
  margin: 0;

  &::placeholder {
    font-style: italic;
  }
}
