@import "../../colors.scss";
@import "../../transitions.scss";
@import "../../icons.scss";

.link {
  z-index: 0;
  color: $antraciet;
  transition: color $mediumEaseInOut, outline-offset $mediumEaseInOut;
  word-break: break-word;
  text-decoration: none;
  position: relative;
  font-weight: bold;

  &__selected {
    outline: none;
    color: $oranje !important;
  }

  &__bold {
    font-weight: bold;
  }

  &:hover {
    color: $oranje !important;
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 4px;
    border-radius: 4px;
    background-color: $antraciet;
    bottom: 0;
    left: 0;
    transform-origin: right;
    transform: scaleX(0);
    transition: transform $mediumEaseInOut;

  }

  &:hover::before {
    transform-origin: left;
    transform: scaleX(1);
  }
}