@import "../../colors.scss";
@import "../../achtergronden.scss";
@import "../../padding.scss";
@import "../../breakpoints.scss";

.container {
  padding: $paddingTall;
  text-align: center;

  @media (max-width: $breakpointDesktopS) {
    padding: $paddingSmall;
  }

  .content {
    max-width: 90em;
    margin: auto;
  }

  &__paars {
    background-image: $illustratiePaars;
    background-color: $paars;
  }

  &__oranje {
    background-image: $illustratieOranje;
    background-color: $oranje;
  }

  &__lijnenOranje {
    background-image: $illustratieLijnenOranje;
    background-color: $paars;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }

  &__lijnenPaars {
    background-image: $illustratieLijnenPaars;
    background-color: $oranje;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }
}