@import "../../colors.scss";
@import "../../breakpoints.scss";

.heading {
  color: $antraciet;
  margin: 0;
  word-break: break-word;
  z-index: 10;

  @media (max-width: $breakpointDesktop) {
    hyphens: auto;
  }

  &__bold {
    font-weight: bold;
  }

  &__default {
    color: $antraciet;
  }

  &__zand {
    color: $zand;
  }

  &__h1 {
    font-size: 3em;
    line-height: 1.1em !important;

    @media (min-width: $breakpointDesktop) {
      font-size: 4em;
      line-height: 1em !important;
    }
  }

  &__h2 {
    font-size: 2.5em;
    line-height: 1.1em !important;

    @media (min-width: $breakpointDesktop) {
      font-size: 3em;
      line-height: 1em !important;
    }
  }

  &__h3 {
    font-size: 2em;
    line-height: 1em !important;

    @media (min-width: $breakpointDesktop) {
      font-size: 2em;
      line-height: 1.5em !important;
    }
  }

  &__h4 {
    font-size: 1.75em;
    line-height: 1em !important;

    @media (min-width: $breakpointDesktop) {
      font-size: 1.5em;
      line-height: 1.5em !important;
    }
  }

  &__text {
    font-size: 1.25em;
    font-style: normal;

    @media (max-width: $breakpointDesktopS) {
      font-size: 1.1em;
    }
  }

  &__preview {
    font-size: 1.25rem;
    font-style: italic;

    @media (max-width: $breakpointDesktopS) {
      font-size: 1.1em;
    }
  }
}
