$space: 4;

.spacer {
    width: 100%;

    &__1 {
        height: #{1*$space}px;
    }

    &__2 {
        height: #{2*$space}px;
    }

    &__4 {
        height: #{4*$space}px;
    }

    &__8 {
        height: #{8*$space}px;
    }

    &__9 {
        height: #{9*$space}px;
    }

    &__16 {
        height: #{16*$space}px;
    }

    &__25 {
        height: #{25*$space}px;
    }

    &__50 {
        height: #{50*$space}px;
    }
}

.spacer_vertical {
    height: 100%;

    &__1 {
        width: #{1*$space}px;
    }

    &__2 {
        width: #{2*$space}px;
    }

    &__4 {
        width: #{4*$space}px;
    }

    &__8 {
        width: #{8*$space}px;
    }

    &__9 {
        width: #{9*$space}px;
    }

    &__16 {
        width: #{16*$space}px;
    }

    &__25 {
        width: #{25*$space}px;
    }

    &__50 {
        width: #{50*$space}px;
    }
}