@import "./../../colors.scss";
@import "./../../padding.scss";
@import "./../../breakpoints.scss";

.fixed_footer {
  width: 100%;
  background: $lichtBlauw;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: -100;

  @media (max-width: $breakpointDesktopS) {
    position: relative;
  }
}

.content {
  padding: 100px 40px 40px 40px;

  @media (max-width: $breakpointDesktopS) {
    padding: $paddingSmall;
  }
}
