@import "../../colors.scss";
@import "../../breakpoints.scss";
@import "../../padding.scss";

.container {
  text-align: center;
  overflow: hidden;

  backdrop-filter: blur(16px) saturate(180%);
  // -webkit-backdrop-filter: blur(16px) saturate(180%);
  // filter: drop-shadow(0 10px 5px rgba(0, 0, 0, 0.125));
  border-radius: 0.9rem;
  // box-shadow: 0px 0px 5px rgb(0 0 0 / 15%);
  --hover: 0;

  :hover {
    --hover: 1;
  }

  .color {
    height: 100%;
    width: 100%;
    --bg: hsl(from #{$oranje} h s calc(90% - (var(--hover) * 10%)) / 50%);
    --accent: hsl(from #{$oranje} h s l);
    transition: background 0.2s;
    background: radial-gradient(
        circle at top left,
        var(--accent),
        transparent 75%
      ),
      var(--bg);

    &--lichtBlauw {
      --bg: hsl(from #{$lichtBlauw} h s l);
      --accent: hsl(from #{$lichtBlauw} h s l);
    }

    &--paars {
      --bg: hsl(from #{$paars} h s l);
      --accent: hsl(from #{$paars} h s l);
      color: $zand !important;
    }

    &--oranje {
      --bg: hsl(from #{$oranje} h s l);
      --accent: hsl(from #{$oranje} h s l);
    }

    .hoverTitle {
      top: 75%;
      position: absolute;
      transform: translateY(calc(var(--hover) * 200%));
    }

    .figure {
      aspect-ratio: 7 / 6;
      padding: 50px 75px;
      display: flex;

      .image {
        width: 100%;
        // height: 100%;
        position: absolute;
        left: 0%;
        top: 0%;
        // transform: translateX(calc(var(--hover) * -15%)) scale(calc(1 + (var(--hover) * 0.2)));
        transform: scale(calc(1 + (var(--hover) * 0.2)));
        transition: transform 0.2s;

        &::before {
          content: "";
          z-index: 1;
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 140%;
          background: linear-gradient(
            to top,
            $oranje,
            $paars
          ); //hsl(from #{$oranje} h s calc(90% - (var(--hover) * 10%)) / 20%);
          opacity: calc(0.45 - (var(--hover) * 1));
        }

        @media (max-width: $breakpointDesktopS) {
          top: 0%;
          bottom: 0%;
        }
      }

      @media (max-width: $breakpointDesktopS) {
        padding: $paddingSmall;
      }
    }
  }
}
