@import "../../colors.scss";
@import "../../breakpoints.scss";
@import "../../padding.scss";

img {
  // -webkit-backdrop-filter: blur(16px) saturate(180%);
  // filter: drop-shadow(0 10px 5px rgba(0, 0, 0, 0.125));
  border-radius: 0.9rem;
  height: auto;
}

.container {
  // position: relative;
  padding: $paddingTall;

  @media (max-width: $breakpointDesktopS) {
    padding: $paddingSmall;
  }

  &__noPadding {
    padding: 0;
  }

  .jumbotronContainer {
    padding-left: 1em;
    display: flex;

    &__left {
      padding-left: 0;
      padding-right: 1em;
    }

    @media (max-width: $breakpointDesktopS) {
      padding: 0em;
    }
  }

  .imageContainer {
    padding-left: 1em;
    position: relative;

    .icon {
      z-index: 10;
      display: inline-table;
      position: absolute;
      top: 18%;
      right: 105%;
      transform: translate(50%, -50%);

      @media (max-width: $breakpointDesktopS) {
        display: none;
      }
    }

    &__left {
      padding-left: 0;
      padding-right: 1em;

      .icon {
        right: 0%;
      }
    }

    @media (max-width: $breakpointDesktopS) {
      padding: 0em;
    }
  }

  .title {
    background-color: $oranje;
    border-radius: 50px;
    padding: 25px 75px;
    margin-bottom: 2em;
    position: relative;

    @media (max-width: $breakpointDesktopS) {
      margin: 0em;
    }
  }
}
