@import "../../breakpoints.scss";
@import "../../colors.scss";
@import "../../padding.scss";

.container {
  background-color: $turqouise;
  max-height: 1000px;
  margin: auto;
  padding: $paddingTall;
  align-content: center;
  max-width: 90em;

  .text {
    max-width: 590px;
  }

  .flex {
    display: flex;
  }

  @media (max-width: $breakpointDesktopS) {
    padding: $paddingSmall;
    display: table;
  }
}
