@import "../fonts.scss";
@import "../colors.scss";
@import "../breakpoints.scss";

.layouts {
  &__page {
    display: flex;
    flex-direction: column;
    // height: 100vh;
    margin: 0;
    // max-height: 100vh;
    min-width: 300px;
    padding: 0;
    width: 100%;

    &__margin {
      margin-bottom: 522px;

      @media (max-width: $breakpointDesktopS) {
        margin-bottom: 0;
      }
    }

    &__content {
      background-color: $turqouise;
      padding-top: 0;

      @media (max-width: $breakpointDesktopS) {
        padding-top: 210px;
      }
    }

    &__body {
      flex: 1;
    }
  }

  &__default {
    &__body {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}

body {
  margin: 0;
  padding: 0;
}

html {
  line-height: 1.5;
  font-size: 1.125em;
  -webkit-font-smoothing: antialiased;
  color: $turqouise;
}

* {
  font-family: $FONT_FAMILY_REGULAR;
}
