@import "../../breakpoints.scss";

.logo {
  position: relative;

  &__icon {
    width: 15px;
    height: 15px;
  }

  &__small {
    width: 30px;
    height: 30px;
  }

  &__medium {
    width: 50px;
    height: 50px;
  }

  &__large {
    width: 200px;
  }

  &__xlarge {
    width: 250px;
  }

  &__xxlarge {
    width: 500px;

    @media (max-width: $breakpointDesktopS) {
      width: 100%;
    }
  }

  &__auto {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}
