@font-face {
  font-family: "Regular";
  src: local("Barlow-Regular"),
    url(../../public/assets/fonts/Barlow-Regular.ttf) format("truetype");
}

@font-face {
  font-family: "Bold";
  src: local("Barlow-Bold"),
    url(../../public/assets/fonts/Barlow-Bold.ttf) format("truetype");
}

@font-face {
  font-family: "Bolder";
  src: local("Barlow-Black"),
    url(../../public/assets/fonts/Barlow-Black.ttf) format("truetype");
  font-weight: 900;
  font-style: normal;
}

$FONT_FAMILY_REGULAR: "Regular";
$FONT_FAMILY_BOLD: "Bold";
$FONT_FAMILY_BOLDER: "Bolder";
