@import "../../colors.scss";

.button {
  background-color: $paars;
  border-radius: 50px;
  font-weight: bolder;
  line-height: 1;
  max-width: 100%;
  font-size: 1.15em;
  margin: 0;
  color: $zand;
  border: 5px solid transparent;
  cursor: pointer;

  & :nth-child(n) {
    cursor: pointer;
  }

  &:hover {
    box-shadow: 0 0 0.75em rgba(21, 66, 115, 0.4);
    background-color: $oranje;
    color: $zand;
    border-color: $paars;
  }

  &--primary {
    background-color: $oranje;
    color: $zand;

    &:hover {
      box-shadow: 0 0 0.75em rgba(21, 66, 115, 0.4);
      background-color: $paars;
      color: $zand;
      border-color: $oranje;
    }
  }
}
