@import "../../breakpoints.scss";

.container {
  max-width: 90em;
  width: 100%;
  grid-gap: 2em;
  display: grid;

  &--alignleft {
    margin: 0;
    padding: 0;
    max-width: 56em;
  }

  &--flex {
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  &--between {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &--grid {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 10px;
  }

  &--row {
    @media (min-width: $breakpointDesktopS) {
      flex-direction: row;
      display: flex;

      > * {
        flex: 1;
      }

      // >*:not(:first-child) {
      //   margin-left: 1em;
      // }
    }

    &--centered {
      align-items: center;
    }
  }

  @media (max-width: $breakpointDesktopS) {
    flex-direction: column;
    grid-gap: 50px;
    display: flex;
  }

  &--centered-horizontal {
    margin: auto;
  }

  &--centered-vertical {
    align-items: center;
  }

  &--small-gap {
    row-gap: 10px;
  }
}
