@import "../../colors.scss";
@import "../../breakpoints.scss";
@import "../../icons.scss";

.topbalk {
  position: fixed;
  width: -webkit-fill-available;
  z-index: 99999;
  background-color: $turqouise;
  padding: 20px 40px;

  & button {
    float: right;
  }

  @media (max-width: $breakpointDesktopS) {
    padding: 20px 20px;

    > div {
      flex-direction: row;
      gap: 20px;
    }
  }

  img {
    cursor: pointer;
  }

  .mobile_nav {
    width: 100%;
    text-align: center;
    border-bottom: 3px $paars solid;

    li {
      display: grid;
      justify-content: center;
      padding: 10px;
    }
  }
}
