@import "../../colors.scss";
@import "../../achtergronden.scss";
@import "../../padding.scss";
@import "../../breakpoints.scss";

.container {
  position: relative;
  padding: $paddingTall;
  max-width: 90em;
  margin: auto;

  @media (max-width: $breakpointDesktopS) {
    padding: $paddingSmall;
  }

  .listWrapper {
    max-height: 600px;
    display: flex;

    @media (max-width: $breakpointDesktopS) {
      max-height: 100%;
      display: block;
    }

    .list::-webkit-scrollbar {
      display: none;
    }

    .list {
      display: flex;
      gap: 4em;
      padding: 0;
      margin: 0 60px;

      list-style: none;
      overflow-x: scroll;
      scroll-snap-type: x mandatory;
      scrollbar-width: none;

      @media (max-width: $breakpointDesktopS) {
        gap: 1em;
        margin: 0;
        max-height: 100%;
      }

      .item {
        display: flex;
        flex-shrink: 0;
        width: 60%;
        max-height: inherit;
        scroll-snap-align: center;

        @media (max-width: $breakpointDesktopS) {
          width: 100%;
        }

        .content {
          display: flex;
          align-items: center;
          justify-content: center;

          @media (max-width: $breakpointDesktopS) {
            display: block;
          }
        }
      }
    }

    .button {
      position: absolute;
      display: flex;
      top: 50%;
      margin: 0;
      padding: 0;
      cursor: pointer;

      &__previous {
        left: 1.5rem;

        transform: rotate(180deg);
      }

      &__next {
        right: 1.5rem;
      }

      @media (max-width: $breakpointDesktopS) {
        top: 0;

        &__previous {
          right: 5rem;
        }
      }
    }
  }
}
