@import "../../colors.scss";

.input {
  &--text {
    display: block;
    width: calc(100% - 30px);
    height: 35px;
    border: 0px;
    background-color: $grijs;
    border-radius: 0.25em;
    padding: 0.6em 0.8em;
    font-size: 1em;
    margin: 0;

    &::-ms-clear {
      display: none;
    }

    &::placeholder {
      font-style: italic;
    }
  }
}
